<template>
  <div id="app">
    <router-view></router-view>
  </div>
</template>

<script>
export default {
  name: 'app',
};
</script>

<style lang="scss">
@import 'base';

/* global styles */

html,
body {
  height: 100vh;
  overflow: hidden;
  margin: 0;
}

#app {
  font-family: 'FF Mark light', sans-serif;
  font-size: 20px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden;
  height: 100vh;
  width: 100%;
  display: flex;
  background: $color-black-pearl linear-gradient(15deg, $color-black-pearl, $color-elephant);

  @include for-size(phone-only) {
    // background-color: red;
  }

  @include for-size(tablet-portrait-only) {
    background-color: blue;
  }

  @include for-size(tablet-landscape-only) {
    background-color: green;
  }
}

div.dg.ac {
  z-index: 1000;
}
</style>



